/* Swipe Cards */

.tinderCards {
  padding-top: 0;
  padding-left: 18%;
}

.card {
  position: relative;
  background-color: #fff;
  width: 400px;
  padding: 5px;
  max-width: 85vw;
  height: 28rem;
  box-shadow: 0px 18px 53px 0px rgba(185, 181, 181, 0.35);
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}

.tinderCards_cardContainer {
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}
.swipe {
  position: absolute;
}

.cardContent {
  width: 100%;
  height: 100%;
}

.card h4 {
  background: rgba(0, 0, 0, 0.4);
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 97.3%;
  color: rgb(253, 251, 251);
  height: 4rem;
  font-family: "Italiana", serif;
  font-size: 3rem;
}

.card h6 {
  background: rgba(0, 0, 0, 1);
  position: absolute;
  bottom: 15%;
  text-align: center;
  width: 97.3%;
  color: rgb(253, 251, 251);
  height: 2rem;
  font-size: 1rem;
  border-radius: 6px;
  padding-top: 1.2%;
}

@media (max-width: 980px) {
  .tinderCards {
    padding-top: 0;
    padding-left: 0;
    margin-top: 5%;
  }
  .card h6 {
    font-size: 0.78rem;
    padding: 2.4%;
  }
  .card h4 {
    padding-top: 3.2%;
    font-size: 2.6rem;
  }
}

/* Loader setting */

.loader {
