  width: 30%;
  height: 4.5rem;
  font-size: 2rem;

  color: black;
  border-radius: 17px;
  box-shadow: 0px 5px orange, -2px 6px orange, -1px 1px orange;
  border: 1px solid orange;
  background-color: #ffeac5;
  transition: 0.5s ease;
}

#btn-explore:hover {
  box-shadow: 0px 10px #ffeac5, -2px 6px #ffeac5, -1px 1px #ffeac5;

  background-color: orange;
}

